* {
  font-family: "Acumin Pro", sans-serif;
  --main-color: #269bfb;
  --foreground: #ffffff;
  --main-color: #269bfb;
  --foreground: #ffffff;
  --sub-1: #5f7bf6;
  --sub-2: #8565f2;
  --gradient: linear-gradient(106.31deg, #269bfb 0%, #8565f2 98.82%);
  --dark: #333;
  --text: #000;
  --bg: whitesmoke;
}

body {
  background: var(--bg);
  margin: 0;
  padding: 0;
}

.App {
  font-family: "Acumin Pro", sans-serif;
  text-align: center;
  color: var(--dark);
}

.logo {
  width: 20vw;
}

.header {
  padding: 0px;
  margin: 0px;
  width: 100%;
  padding-top: 20vh;
  padding-bottom: 1vh;
  margin-bottom: 4vh;
  background: var(--gradient);
  color: var(--foreground);
  text-align: left;
  font-size: 2em;
  padding-left: 20vw;
}

.center {
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}

.lb {
  padding: 0px;
  margin: 0px;
  width: 100%;
  padding-top: 2vh;
  padding-bottom: 2vh;
  margin-top: 4vh;
  margin-bottom: 4vh;
  background: var(--gradient);
  color: var(--foreground);
  text-align: center;
  font-size: 1.5em;
}

.ft {
  padding: 0px;
  margin: 0px;
  width: 100%;
  padding-top: 2vh;
  padding-bottom: 2vh;
  background: var(--gradient);
  color: var(--foreground);
  text-align: center;
  font-size: 1em;
  bottom: 0px;
}

.login label {
  float: left;
}

.login {
  width: 400px;
  min-width: 10vw;
  max-width: 90vw;
}

.login a:not(.submit) {
  color: var(--dark);
  text-decoration: none;
  position: relative;
  font-size: 12px;
}

.right {
  float: right;
}

.login span {
  color: var(--main-color);
  text-decoration: none;
  font-size: 12px;
}

.login a:not(.submit):hover {
  color: var(--dark);
  text-decoration: underline;
  position: relative;
  font-size: 12px;
  float: right;
}

.login label {
  color: var(--main-color);
}

.submit {
  display: block;
  width: 100% !important;
}

.login input,
.submit {
  width: 100%;
  padding: 10px 0 10px 0px;
  margin-top: 10px;
  margin-bottom: 30px;
  background: none;
}

.login input[type="text"],
input[type="password"] {
  outline: 0;
  border-width: 0 0 2px;
  border-color: var(--main-color);
}

.login input[type="text"]:hover,
input[type="password"]:hover {
  outline: 0;
  border-width: 0 0 2px;
  border-color: var(--sub-2);
}

.login input[type="submit"],
.submit {
  text-decoration: none;
  margin-top: 75px;
  outline: 0;
  border-width: 0px;
  border-radius: 10px;
  width: 100%;
  background: var(--main-color);
  color: var(--foreground);
}

.login input[type="submit"]:hover,
.submit:hover {
  margin-top: 75px;
  outline: 0;
  border-width: 0px;
  border-radius: 10px;
  width: 100%;
  background: var(--gradient);
  color: var(--foreground);
}

.lnk:before {
  font-family: "Font Awesome 5 Free";
  content: "\f0c1";
  display: inline-block;
  padding-right: 3px;
  vertical-align: middle;
  font-weight: 900;
}

label[for="email"]:before {
  font-family: "Font Awesome 5 Free";
  content: "\f0e0";
  display: inline-block;
  padding-right: 3px;
  vertical-align: middle;
  font-weight: 900;
}

label[for="password"]:before {
  font-family: "Font Awesome 5 Free";
  content: "\f084";
  display: inline-block;
  padding-right: 3px;
  vertical-align: middle;
  font-weight: 900;
}

