  ); */
  font-family: acumin-pro, sans-serif;
  font-weight: 400;
  font-style: normal;
}

.hp-section1 {
  background-color: #000000;
  height: 480px;
}

.hp-nav > img {
  margin: 40px;
  height: 30px;
}

.hp-section1 > p {
  color: #ffffff;
  font-weight: 500;
  font-size: 24px;
  margin-top: 40px;
}

.hp-section1 > .content {
  font-size: 16px;
  font-weight: 100;
  margin-top: 10px;
  margin-bottom: 40px;
}

.hp-btn-explore {
  margin-top: 20px;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(9, 9, 121, 1) 35%,
    rgba(1, 42, 50, 1) 100%
  );
  color: #ffffff;
  font-size: 18px;
  padding: 14px 60px;
  border-radius: 12px;
}

.hp-section2 {
  height: 480px;
}

.hp-s2-header > p {
  color: #000000;
  font-size: 36px;
  font-weight: bold;
  margin-top: 24px;
}

.hp-exp {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 24px;
}

.hp-exp > div {
  width: 560px;
}

.hp-exp-head {
  font-weight: bold;
  font-size: 20px;
  margin-bottom: 48px;
}

.hp-section3 {
  background-color: #000000;
  height: 700px;
}

.hp-s3-header > p {
  padding-top: 40px;
  color: #ffffff;
  font-size: 36px;
  font-weight: bold;
}

.hp-exp3 {
  display: flex;
  flex-direction: row;
  flex-wrap: row;
  justify-content: center;
  align-items: center;
}

.hp-exp3 > img {
  margin: 20px 20px;
  padding: 20px 20px;
}

.hp-exp3 > div {
  width: 560px;
  color: #ffffff;
}
