* {
  margin: 0px;
  padding: 0px;
}
.team {
  background: #000000;
  margin: 0px;
  padding: 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.team > p {
  font-size: 64px;
  color: #ffffff;
}

.team-member {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: flex-start;
}

.team-member > img,
h1 {
  height: 240px;
  width: 210px;
  margin-right: 24px;
  color: #ffffff;
  margin-top: 20px;
  margin-bottom: 20px;
}
