.gr-nav {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: flex-start;
  margin: 10px 10px;
}

.gr-h {
  color: #000000;
  text-align: center;
  margin-left: 100px;
}

.gr-sub {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: flex-start;
  margin: 10px 10px;
}

.gr-sub-card {
  width: 300px;
  background-color: #000000;
  border-radius: 12px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  color: #ffffff;
  margin: 10px 140px;
}

.gr-sub-chat {
  height: 600px;
  background-color: #000000;
  padding: 20px;
  color: #ffffff;
}

.gr-box {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  padding: 20px 10px;
}

.gr-text {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 20px 10px;
}

.gr-msg {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 20px 10px;
}

.gr-msg > input {
  width: 70%;
  height: 32px;
  border-radius: 6px;
}
