.room {
  width: 90%;
  margin-top: 10px;
  height: 174px;
  left: 21px;
  top: 176px;
  align-self: center;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(9, 9, 121, 1) 35%,
    rgba(1, 42, 50, 1) 100%
  );
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding-left: 20px;
}

.room > p {
  color: #ffffff;
  font-size: 17px;
  text-align: left;
  padding-top: 20px;
}

Button {
  background: linear-gradient(
    180.21deg,
    rgba(255, 255, 255, 0) 0.18%,
    #ffffff 53.69%,
    #ffffff 53.69%
  );
}

.room > div > i {
  color: #ffffff;
  margin: 0px 80px;
}
